body {
  margin: 0;
  padding: 0;
  background-image: url('back.gif');
}
h1 {
  font-size: 30px;
  color: #fff;
  margin-bottom: 20px;
  text-align: center;
}
.movie-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.movie-row {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px;
}

.card {
  width: 21%;
  background-color: #250885;
  box-shadow: 0 2px 4px rgba(29, 30, 18, 0.1);
  border: 5px solid red;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.card-img {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.card-title {
  font-size: 20px;
  font-weight: 100;
  margin-bottom: 10px;
  margin-left: 110px;
  color: rgb(16, 228, 69);
}

.watch-btn {
  background-color: #e50914;
  color: #fff;
  border: none;
  margin-left: 110px;
  padding: 10px 15px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 5px;
}

.watch-btn:hover {
  background-color: #c40611;
}
